<template>
    <div class="qiong-td qiong-td-h-full qiong-line-hight-15">

            <div class="qiong-wide-10">{{ _item.number_code }}</div>
            <div class="qiong-wide-11">{{ view.if_def(_item.sponser_id, 'yourself') }}</div>
            <div class="qiong-wide-9 text-center">{{ index }}</div>
            <div class="qiong-wide-12 text-center pr-0">{{ _item.transfer.son_can_num_ENROLLER }}</div>
            <div class="qiong-wide-6">{{ _item.ipv }}</div>

            <div class="qiong-wide-11 text-center pr-0">{{ _item.ipv_first }}</div>
            <div class="qiong-wide-11 text-center">{{ view.if_def(usd_ratio) }}</div>
            <div class="qiong-wide-12 text-center">{{ usd }}</div>
            <div class="qiong-wide-13 text-center pr-0">{{ _item.transfer.son_pv_ENROLLER }}</div>
            <div class="qiong-wide-5">
                <qiong-icon-inset :color="''" 
                    v-if="_item.index != 0 && _item.has_child"
                    :arg="_item" @done_Father="openCenter"
                ></qiong-icon-inset>
            </div>
    </div>
</template>

<script>
import QiongIconInset from '../../../../components/Qiong/Button/QiongIconInset.vue'
    export default {
  components: { QiongIconInset },
        inject: [ 'reload' ],
        props: {
            _item: {
                type: Object
            },
            me: {
                type: Object
            }
        },
        name: '',
        data() {
            return {
                ratios: [ ]
            }
        },
        created() {
            this.ratios = this.bonus.conf.RECOMMEND_RATIOS
        },
        computed: {
            index() {
                return this._item.index - this.me.index
            },

            usd_ratio() {
                let res = this.ratios[ this._item.index - this.me.index - 1 ]
                if (res) {
                    return this.view.floatMul(res,  100) + '%'
                }
                return ''
            },
            usd() {
                let res = this.ratios[ this._item.index - this.me.index - 1 ]

                if (this._item.unid == this.me.unid) {
                    return this.me.bonus.usd_recommend
                }
                if (res) {
                    return this.view.floatMul(res,  this._item.ipv_first)
                }
                return '0'
            }
        },
        methods: {
            openCenter(item) {
                // /home/user/sponser


                this.$router.push({
                    path: '/home/user/sponser', query: { 'id': item.unid } })

                this.reload()
            }
        },
    }
</script>

<style lang="sass" scoped>
    
</style>


